const setProducts = (products) => {
  return { type: "setProducts", payload: products };
};

const setCurrentProductId = (currentProductId) => {
  return { type: "setCurrentProductId", payload: currentProductId };
};

const setSearchedProduct = (string) => {
  return { type: "setSearchedProduct", payload: string };
};

const setMenuToggle = (EpisodeId) => {
  return { type: "setMenuToggle" };
};

export { setProducts, setCurrentProductId, setSearchedProduct, setMenuToggle };
