import React from "react";
import { useDispatch } from "react-redux";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { setMenuToggle } from "../../actions";
import "./Header.css";
import CodeValue from "../../assets/images/CodeValue.png";

const Header = () => {
  const dispatch = useDispatch();

  const handleMenuClick = () => {
    dispatch(setMenuToggle());
  };

  return (
    <div>
      <ToastContainer
        className="col-10 col-sm-2 ms-3 ms-s mt-3"
        position="top-center"
      />
      <nav className="navbar pb-0 bg-light shadow-sm px-5 border-bottom">
        <div className="d-flex align-items-center">
          <div
            className="menu"
            onClick={handleMenuClick}
            title="Products Details Container Toggle"
          >
            <span></span>
            <span></span>
            <span></span>
          </div>
          <p className="display-6 ms-3 pt-2">CodeValue‚Äè‚Äè Assignment</p>
          <p className="display-6 ms-2 fs-6 pt-3">By David Saal</p>
        </div>
        <div>
          <img className="pb-1" src={CodeValue} alt="Code Value" />
        </div>
      </nav>
    </div>
  );
};

export default Header;
