import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setProducts } from "../../actions";
import { toast } from "react-toastify";
import image from "../../assets/images/image.png";

const ProductDetailsContainer = () => {
  const dispatch = useDispatch();
  let products = useSelector((state) => state.products);
  let currentProductId = useSelector((state) => state.currentProductId);
  let currentProduct = products.find(
    (product) => currentProductId === product.id
  );

  const [editedProduct, setEditedProduct] = useState({
    id: 1,
    name: "",
    description: "",
    price: 0,
  });

  const handleOnChange = (event) => {
    const { name, value } = event.target;
    setEditedProduct({ ...editedProduct, [name]: value });
  };

  const handleOnClick = () => {
    let index = products.findIndex(
      (product) => product.id === editedProduct.id
    );
    products[index] = editedProduct;
    dispatch(setProducts(products));
    toast.success(`'${editedProduct.name}' edited successfully.`);
  };

  useEffect(() => {
    currentProduct &&
      setEditedProduct({
        id: currentProduct.id,
        name: currentProduct.name,
        description: currentProduct.description,
        price: currentProduct.price,
      });
  }, [currentProduct]);

  return (
    <div className="card shadow p-3 mt-3 mt-sm-none col-11 col-sm-4">
      {products.length > 0 ? (
        <div>
          <div className="container">
            <img className="shadow" src={image} alt="product" />
            <h4 className="mt-3">Name</h4>
            <input
              type="text"
              className="form-control"
              value={editedProduct.name}
              name="name"
              onChange={handleOnChange}
            />
            <h4 className="mt-3">Description</h4>
            <input
              type="text"
              className="form-control"
              value={editedProduct.description}
              name="description"
              onChange={handleOnChange}
            />
            <h4 className="mt-3">Price</h4>
            <div className="input-group">
              <input
                type="number"
                className="form-control"
                value={editedProduct.price}
                name="price"
                onChange={handleOnChange}
              />
              <span className="input-group-text">$</span>
            </div>
          </div>
          <div className="d-flex justify-content-center">
            <button className="btn btn-dark mt-4 col-5" onClick={handleOnClick}>
              Save
            </button>
          </div>
        </div>
      ) : (
        <h1 className="text-danger ">Product list is empty.</h1>
      )}
    </div>
  );
};

export default ProductDetailsContainer;
