import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { setCurrentProductId, setProducts } from "../../actions";
import arrow from "../../assets/images/arrow.png";
import image from "../../assets/images/image.png";
import { toast } from "react-toastify";
import "./SideNavItem.css";

const SideNavItem = ({ id, name, description }) => {
  const dispatch = useDispatch();
  let products = useSelector((state) => state.products);
  let currentProductId = useSelector((state) => state.currentProductId);

  const handleProductClick = () => {
    dispatch(setCurrentProductId(id));
  };

  const handleDeleteProduct = () => {
    products = products
      .filter((product) => product.id !== id)
      .map((product, index) => ({ ...product, id: index + 1 }));
    dispatch(setProducts(products));
    toast.success(`'${name}' removed from your list.`);
  };

  return (
    <a
      href="#"
      onClick={() => handleProductClick()}
      className={`list-group-item list-group-item-action d-flex justify-content-between align-items-center p-2 ${
        currentProductId === id && "active"
      }`}
    >
      <div className="d-flex align-items-center gap-3">
        <img src={image} alt="Product" />
        <div>
          <h3>{name}</h3>
          <p className="display-6 fs-4">{description}</p>
          <button
            className="btn btn-dark p-0 px-2"
            onClick={handleDeleteProduct}
          >
            Delete
          </button>
        </div>
      </div>
      <img src={arrow} alt="Click" />
    </a>
  );
};

export default SideNavItem;
