import { combineReducers } from "redux";

import productsReducer from "./products";
import currentProductIdReducer from "./currentProductId";
import searchedProductReducer from "./searchedProduct";
import menuToggleReducer from "./menuToggle";

const allReducers = combineReducers({
  products: productsReducer,
  currentProductId: currentProductIdReducer,
  searchedProduct: searchedProductReducer,
  menuToggle: menuToggleReducer,
});

export default allReducers;
